import React from 'react'
import Mstyle from './Motors.module.css'
import { Button, Container, Nav, Navbar, Col } from 'react-bootstrap'

function Navbars() {
    return (
        <div>
            <Col lg={12} sm={12}>
                <marquee width='100%' direction='right'>Free Shipping</marquee></Col>
            <Navbar expand='lg'>
                <Container>
                    <Navbar.Brand className={Mstyle.brand_name}>Motor <span>pro gear</span></Navbar.Brand>
                    <Navbar.Toggle></Navbar.Toggle>
                    <Navbar.Collapse>
                        <Nav className='ms-auto'>
                            <Nav.Link className={Mstyle.brand_list}>Home</Nav.Link>
                            <Nav.Link className={Mstyle.brand_list}>Brands</Nav.Link>
                            <Nav.Link className={Mstyle.brand_list}>Products</Nav.Link>
                            <Nav.Link className={Mstyle.brand_list}>Track Order</Nav.Link>
                            <Nav.Link className={Mstyle.brand_list}>Support</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </div>

    )
}

export default Navbars;