import React from "react";
import Mstyle from './Motors.module.css'
import { Card, Row, Col, Button } from 'react-bootstrap'

function Cardproducts() {
    return (
        <div className={Mstyle.pruchase_list}>
            <h1 className='text-center mb-5'>Start Purchasing</h1>
            <Row>
                <Col sm={12} lg={3} className={Mstyle.lists}>
                    <Card>
                        <Card.Img src='https://ls2helmetsindia.com/environment/cache/images/500_500_productGfx_cf942022c2c5f0eb360e4e774dea8486.jpg' className={Mstyle.helmet_img}></Card.Img>
                        <Card.Body>
                            <Card.Title className='text-center'>Ls2</Card.Title>
                            <Card.Text>LS2 helmets are one of the few choices in the safety helmet industry which gives you the best of both worlds. The helmets are born classy and exude confidence and comfort.</Card.Text>
                            <Button variant='danger' className='me-2'>Buy Now</Button>
                            <Button variant='warning'>Add Whishlist</Button>
                        </Card.Body>
                    </Card>
                </Col>
                <Col sm={12} lg={3} className={Mstyle.lists}>
                    <Card>
                        <Card.Img src='https://www.motardinn.com/f/13750/137504922/mt-helmets-thunder-3-sv-turbine-full-face-helmet.jpg' className={Mstyle.helmet_img}></Card.Img>
                        <Card.Body>
                            <Card.Title className='text-center'>MT</Card.Title>
                            <Card.Text>Is MT a good helmet brand?
                                MT Helmets are made by the largest motorcycle helmet company in Spain and is one of the most reputed brands in Europe.</Card.Text>
                            <Button variant='danger' className='me-2'>Buy Now</Button>
                            <Button variant='warning'>Add Whishlist</Button>
                        </Card.Body>
                    </Card>
                </Col>
                <Col sm={12} lg={3} className={Mstyle.lists}>
                    <Card>
                        <Card.Img src='https://www.customelements.in/wp-content/uploads/2020/02/AGV-Pista-GP-R-Rossi-Winter-Test-2019-Full-Face-Helmet.jpg' className={Mstyle.helmet_img}></Card.Img>
                        <Card.Body>
                            <Card.Title className='text-center'>AGV</Card.Title>
                            <Card.Text>AGV makes some terrific full face helmets. Their range includes several carbon fiber shelled lids as well as composite and lower priced thermoplastic helmets.</Card.Text>
                            <Button variant='danger' className='me-2'>Buy Now</Button>
                            <Button variant='warning'>Add Whishlist</Button>
                        </Card.Body>
                    </Card>
                </Col>
                <Col sm={12} lg={3} className={Mstyle.lists}>
                    <Card>
                        <Card.Img src='https://www.shoei-helmets.com/pub/media/catalog/product/cache/1/image/700x560/e9c3970ab036de70892d86c6d221abfe/x/-/x-fourteen-mm93-black-concept-2.0-tc-1.jpg' className={Mstyle.helmet_img}></Card.Img>
                        <Card.Body>
                            <Card.Title className='text-center'>SHOEI</Card.Title>
                            <Card.Text>Shoei has a focus on the rider's comfort, so they don't get exhausted by fatigue caused by wind resistance or overwhelmed by noise turbulence.</Card.Text>
                            <Button variant='danger' className='me-2'>Buy Now</Button>
                            <Button variant='warning'>Add Whishlist</Button>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </div>
    )
}

export default Cardproducts;