import React from 'react'
import Mstyle from './Motors.module.css'
import { Image } from 'react-bootstrap'

function Products() {
    return (
        <div className={Mstyle.our_products}>
            <h1 className='text-center'>Our Products</h1>
            <div className={Mstyle.brand_our_products}>
                <div className={Mstyle.brannd_split}>
                    <Image src='https://i.pinimg.com/originals/97/23/84/9723843677afe05e79ff5f6b3ef3753c.jpg' className={Mstyle.brand_products}></Image>
                    <div className={Mstyle.overlay}>
                        <div className={Mstyle.content}>
                            <h3>Helmets</h3>
                        </div>
                    </div>
                </div>
                <div className={Mstyle.brannd_split}>
                    <Image src='https://rukminim1.flixcart.com/image/416/416/jpu324w0/rider-protective-jacket/b/e/h/3xl-bbg-06-biking-brotherhood-original-imafbzf4u4yfw8rz.jpeg?q=70' className={Mstyle.brand_products}></Image>
                    <div className={Mstyle.overlay}>
                        <div className={Mstyle.content}>
                            <h3>Jackets</h3>
                        </div>
                    </div>
                </div>
                <div className={Mstyle.brannd_split}>
                    <Image src='https://www.trialsbike.com.au/wp-content/uploads/2018/11/JI19PATR-1015_1-1.jpg' className={Mstyle.brand_products}></Image>
                    <div className={Mstyle.overlay}>
                        <div className={Mstyle.content}>
                            <h3>Pants</h3>
                        </div>
                    </div>
                </div>
                <div className={Mstyle.brannd_split}>
                    <Image src='https://images-na.ssl-images-amazon.com/images/I/71ubzWjjjyL._AC_SL1500_.jpg' className={Mstyle.brand_products}></Image>
                    <div className={Mstyle.overlay}>
                        <div className={Mstyle.content}>
                            <h3>Gloves</h3>
                        </div>
                    </div>
                </div>
                <div className={Mstyle.brannd_split}>
                    <Image src='https://bikegear.in/image/cache/catalog/Forma-boots/forma-adventure-black-boots-1-600x600.jpg' className={Mstyle.brand_products}></Image>
                    <div className={Mstyle.overlay}>
                        <div className={Mstyle.content}>
                            <h3>Boots</h3>
                        </div>
                    </div>
                </div>
                <div className={Mstyle.brannd_split}>
                    <Image src='http://cdn.shopify.com/s/files/1/0010/0836/9711/products/GT2-black.png?v=1598477868' className={Mstyle.brand_products}></Image>
                    <div className={Mstyle.overlay}>
                        <div className={Mstyle.content}>
                            <h3>Backbags</h3>
                        </div>
                    </div>
                </div>
                <div className={Mstyle.brannd_split}>
                    <Image src='https://bdlamotorbikes.co.uk/wp-content/uploads/2021/05/MT-V-14-Motorcycle-Helmet-Visor-Dark-1.jpg' className={Mstyle.brand_products}></Image>
                    <div className={Mstyle.overlay}>
                        <div className={Mstyle.content}>
                            <h3>Helmet Accessories</h3>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Products;