import React from 'react'
import Mstyle from './Motors.module.css'
import { } from 'react-bootstrap'

function Footers() {
    return (
        <div className={Mstyle.footers}>
            <p>Sign up for our news letter</p>
            <p>Subscribe to know about our product launches, features, etc.</p>
            <div className={Mstyle.social_media}>
                <a href="">Facebook</a>
                <a href="">Twitter</a>
                <a href="">Instagram</a>
                <a href="">Whatsapp</a>
            </div><br></br>
            <div className={Mstyle.contact}>
                <a href="">Contact Us</a>
                <a href="">About Us</a>
            </div>
        </div >
    )
}

export default Footers;