import React from 'react'
import Mstyle from './Motors.module.css'
import { Form, Row, Col, Button } from 'react-bootstrap'

function Review() {
    return (
        <div>
            <div className={Mstyle.brand_review}>
                <Row className={Mstyle.brand_row}>
                    <Col sm={12} md={7}>
                        <p className={Mstyle.brand_para}>
                            Motor ProGear, popularly known as SPG is the leading manufacturer, importer and distributor of all categories of riding gear for bikers in India. Our Mission - Making safety affordable for all bikers without any compromise on quality. Being a company started and run by bikers, our products are meticulously designed with emphasis on Practicality, Functionality and Style. Our product range includes Motorcycle Helmets, Riding Jackets, Riding Pants, Riding Gloves, Motorcycle Boots and other accessories for bikers. We believe in ATGATT....... All The Gear All The Time.
                        </p>
                    </Col>
                    <Col sm={12} md={5}>
                        <Form className={Mstyle.brand_forms}>
                            <h3 className='text-center mb-3'>Submit Your Feedback</h3>
                            <Form.Group>
                                <Form.Control type='text' placeholder='FullName' className='mb-2'></Form.Control>
                            </Form.Group>
                            <Form.Group>
                                <Form.Control type='email' placeholder='Email Id' className='mb-2'></Form.Control>
                            </Form.Group>
                            <Form.Group>
                                <Form.Control type='number' placeholder='Mobile Number' className='mb-2'></Form.Control>
                            </Form.Group>
                            <Form.Group>
                                <Form.Control as='textarea' placeholder='Feedbacks' row={3} className='mb-2'></Form.Control>
                            </Form.Group>
                            <Button type='submit' variant='success' size='sm'>Submit</Button>
                        </Form>
                    </Col>
                </Row>
            </div>
        </div >
    )
}

export default Review;