import React from 'react'
import Mstyle from './Motors.module.css'
import { Image, Row, Col } from 'react-bootstrap'

function Brands() {
    return (
        <div className={Mstyle.images}>
            <h1 className='text-center'>Brands</h1>
            <div className={Mstyle.brand_images}>
                <Image src='https://aspidagroup.com/wp-content/uploads/2020/05/Aspida-logo-_Artboard-1-1-1.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://pinlock.com/wp-content/uploads/2020/01/V-helmets-logo.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://probikerhelmets.com/wp-content/uploads/2020/09/ryo-logo.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://axxis-helmets.com/wp-content/uploads/axxis-logo-banner-1-768x310.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://fontmeme.com/images/Wrangler-Logo.jpg' className={Mstyle.brand_images_img}></Image>
                <Image src='https://i.pinimg.com/originals/94/05/78/940578e6f3c3121e9a9522c36a2f0bdd.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://my-test-11.slatic.net/p/971c754727eeba097ac97f68845c9367.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://seeklogo.net/wp-content/uploads/2013/03/hjc-vector-logo-400x400.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://logodix.com/logo/1621340.png' className={Mstyle.brand_images_img} className={Mstyle.brand_images_img}></Image>
                <Image src='https://logoeps.com/wp-content/uploads/2013/05/shoei-vector-logo.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://powersportsoutlet.com/image/cache/catalog/brands/ls2-logo-225x225.png' className={Mstyle.brand_images_img}></Image>
                <Image src='https://lezebre.lu/images/detailed/18/30960-MT_Helmets.png' className={Mstyle.brand_images_img}></Image>
            </div>
        </div>
    )
}

export default Brands;