import React from 'react'
import Mstyle from './Motors.module.css'
import { Carousel, Image } from 'react-bootstrap'

function Slideshow() {
    return (
        <div>
            <Carousel className={Mstyle.Sliders} fade variant='dark'>
                <Carousel.Item>
                    <Image src='https://www.pramacracing.com/wp-content/uploads/2020/07/A926405-scaled.jpg' className='w-100 d-block' rounded></Image>
                </Carousel.Item>
                <Carousel.Item>
                    <Image src='https://cdn-attachments.timesofmalta.com/0054a14926597ec3db249ceba26437252cd9716b-1568472728-5d7cfe98-1920x1280.jpg' className='w-100' rounded></Image>
                </Carousel.Item>
                <Carousel.Item>
                    <Image src='https://wallpaperaccess.com/full/2267733.jpg' className='w-100' rounded></Image>
                </Carousel.Item>
                <Carousel.Item>
                    <Image src='https://www.teahub.io/photos/full/24-245913_motogp-wallpaper-valentino-rossi-motogpic-pinterest-valentino-rossi.jpg' className='w-100' rounded></Image>
                </Carousel.Item>
            </Carousel>
        </div>
    )
}

export default Slideshow;